/* -------------------------------------
hero section
------------------------------------- */
.hero-sec {
  height: 85vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: relative;
  z-index: 1;
}
.hero-sec::before {
  content: "";
  background-color: #0000008a;
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  z-index: -1;
}
.hero-sec h1 {
  font-size: 3rem;
  font-weight: 900;
  margin: 0;
  margin-bottom: 15px;
  text-align: center;
}
.hero-sec h4 {
  font-size: 1.5rem;
  font-weight: 400;
  margin: 0;
  text-align: center;
}
.hero-sec p {
  font-weight: 400;
  font-size: 1.25rem;
  line-height: 1.875rem;
  text-align: center;
}
.search-bar {
  display: flex;
  align-items: center;
  justify-content: center;
}
.btn-primary-second {
  width: auto;
  font-size: 1.5rem;
  font-weight: 500;
  height: 56px;
  padding: 0.75rem 1.5rem;
  background: rgb(229, 9, 20);
  color: rgb(255, 255, 255);
  border-radius: 0.25rem;
  border: 0;
  margin-left: 10px;
  cursor: pointer;
}
.r-arrow {
  width: 17px;
  margin-left: 5px;
}
/* float input */
/* Input style */
.input-container {
  position: relative;
  display: flex;
  flex-direction: column;
}

.input-container:focus-within label {
  transform: translate(0, 12px) scale(0.8);
  color: #ffffff;
}
.input-container input[value="demo"] label {
  transform: translate(0, 12px) scale(0.8);
  color: #ffffff;
}
.input-container .filled {
  transform: translate(0, 12px) scale(0.8);
}

.input-container label {
  position: absolute;
  pointer-events: none;
  transform: translate(0, 20px) scale(1);
  transform-origin: top left;
  transition: 200ms cubic-bezier(0, 0, 0.2, 1) 0ms;
  color: #ffffff;
  font-size: 16px;
  line-height: 1;
  left: 16px;
}

.input-container input {
  color: #ffffff;
  height: 26px;
  border-radius: 0.25rem;
  border: none;
  padding: 24px 16px 4px 16px;
  font-size: 16px;
  line-height: 1;
  outline: none;
  box-shadow: none;
  transition: 200ms cubic-bezier(0, 0, 0.2, 1) 0ms;
  background-color: #00000047;
  background: #00000047;
  min-width: 351px;
  border: 0.1px solid #fff;
}

.input-container input:focus {
  box-shadow: 0 0 0 2px #ffffff;
}
@media (max-width: 991px) {
  .hero-sec h1 {
    font-size: 2.3rem;
  }
}
@media (max-width: 767px) {
  .hero-sec {
    padding: 100px 15px 0;
    height: 60vh;
  }
  .hero-sec h1 {
    font-size: 2rem;
  }
  .hero-sec h4 {
    font-size: 1.2rem;
  }
  .hero-sec p {
    font-size: 0.75em;
    line-height: unset;
  }
  .search-bar {
    flex-direction: column;
  }
  .input-container {
    margin-right: 0;
  }
  .btn-primary-second {
    margin-top: 15px;
    font-size: 1.2rem;
    height: 48px;
  }
  .r-arrow {
    width: 13px;
  }
  .input-container input {
    height: 18px;
    min-width: 250px;
  }
  .input-container label {
    transform: translate(0, 16px) scale(1);
  }
}
