/* -------------------------------------
signin page
------------------------------------- */
.sign-in-sec {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: relative;
  padding: 100px 0;
  z-index: 1;
}
.sign-in-sec::before {
  content: "";
  background-color: #0000008a;
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  z-index: -1;
}
.signin-box {
  background-color: #000000ba;
  padding: 60px;
  border-radius: 0.75rem;
  width: 400px;
}
.signin-box h2 {
  color: #fff;
  font-size: 2rem;
  font-weight: 500;
  margin-bottom: 28px;
}
.btn-primary-signin {
  width: 100%;
  font-size: 1.2rem;
  font-weight: 500;
  height: 48px;
  padding: 0.75rem 1.5rem;
  background: rgb(229, 9, 20);
  color: rgb(255, 255, 255);
  border-radius: 0.25rem;
  border: 0;
  cursor: pointer;
}
.btn-primary-signin:disabled {
  cursor: not-allowed !important;
  opacity: 0.5;
}
.btn-primary-signin:disabled .lds-ring {
  display: inline-block;
}
.rememberAndHelp label {
  font-size: 12px;
  color: #d0d0d0;
}
.rememberAndHelp a {
  font-size: 12px;
  color: #d0d0d0;
}
.rememberAndHelp a:hover {
  text-decoration: underline;
}
.newToText {
  font-size: 14px;
  color: #ccc;
}
.newToText a {
  font-weight: 500;
  color: #fff;
}
.newToText a:hover {
  text-decoration: underline;
}
.learnDesc p {
  margin-top: 30px;
  margin-bottom: 0;
  font-size: 12px;
  text-align: left;
  line-height: normal;
}
.learnDesc p a {
  color: #00b2ff;
}
.learnDesc p a:hover {
  text-decoration: underline;
}
.input-container:hover .password-btn {
  visibility: visible;
  opacity: 1;
}
.password-btn {
  position: absolute;
  right: 1px;
  top: 1px;
  bottom: 1px;
  padding: 0 15px;
  border: 0;
  cursor: pointer;
  background-color: #292929;
  color: #ccc;
  font-size: 14px;
  text-transform: uppercase;
  letter-spacing: 1px;
  border-radius: 0 0.25rem 0.25rem 0;
  display: flex;
  align-items: center;
  justify-content: center;
  visibility: hidden;
  opacity: 0;
}
/* -------------------------------------
Spinner
------------------------------------- */
.lds-ring {
  position: relative;
  width: 10px;
  height: 10px;
  border-radius: 50%;
  border-top: 2px solid #ffffff;
  border-bottom: 2px solid #ffffff;
  border-left: 2px solid #ffffff;
  border-right: 2px solid #e50a15;
  display: none;
}
@media (prefers-reduced-motion: no-preference) {
  .lds-ring {
    animation: spinner infinite 1s linear;
  }
}
/* -------------------------------------
Single Movie 
------------------------------------- */
.single-movie-sec {
  overflow: hidden;
  padding: 0 15px;
  margin: 83px 0 60px 0;
}
.single-card {
  display: flex;
  flex-direction: column;
  align-items: baseline;
  justify-content: flex-end;
  background-size: cover;
  height: 350px;
  border-radius: 17px;
  border: 1px solid #ffffff;
  position: relative;
  z-index: 1;
}
.single-card::before {
  content: "";
  position: absolute;
  background-color: #00000082;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  border-radius: 17px;
  z-index: -1;
}
.single-card-content {
  padding: 40px;
}
.single-card-content h3 {
  font-size: 40px;
  margin: 0;
  margin-bottom: 15px;
}
.single-card-content p {
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
}
@media (max-width: 767px) {
  .single-card {
    background-size: 100% 100%;
    height: 450px;
  }
  .single-movie-sec {
    margin-top: 62px 0 60px 0;
  }
}
@keyframes spinner {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
@media (max-width: 767px) {
  .signin-box {
    padding: 35px;
    width: 80%;
  }
  .hero-sec.sign-in-sec {
    padding: 150px 15px 50px;
    height: 60vh;
  }
}
